@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Work+Sans:wght@300;700&display=swap");

:root {
  /* Variables */
  --light-mode-bg-color: rgb(226, 60, 60);
  --light-mode-border-color: rgb(255, 44, 44);
  --dark-mode-bg-color: rgb(175, 46, 46);
  --dark-mode-border-color: rgb(195, 46, 46);
}

* {
  margin: 0px;
  padding: 0px;
  text-align: center;
}

body {
  background-color: rgb(226, 34, 34);
  color: white;
}

div.light-mode {
  background-color: var(--light-mode-bg-color);
}

div.april-fools-light-mode {
  background-color: yellow;
  color: black;
}

div.dark-mode {
  background-color: var(--dark-mode-bg-color);
}

h1 {
  font-family: cursive;
}

.m-5px {
  margin: 5px;
}

.m-30px {
  margin: 30px;
}

.mb-30px {
  margin-bottom: 30px;
}

button {
  font-family: "Roboto Mono", monospace;
  font-size: 150%;
  color: white;
}

.active {
  border: 3px solid green;
  background-color: rgb(0, 68, 0);
}

.active:hover {
  cursor: pointer;
}

.inactive {
  border: 3px solid red;
  background-color: rgb(109, 0, 0);
}

.inactive:hover {
  cursor: pointer;
}

.big-text {
  font-size: 1.875rem;
}

.description {
  font-size: 1.5rem;
  font-family: "Oswald", sans-serif;
  margin-top: 5px;
  margin-bottom: 50px;
}

.legend-name {
  font-family: "Oswald", sans-serif;
}

.activator {
  opacity: 1;
}

@media (max-width: 520px) {
  .activator.light-mode {
    border: 3px solid var(--light-mode-border-color);
    border-radius: 8px;
    background-color: var(--light-mode-bg-color);
    transition-duration: 1s;
    color: white;
  }

  .activator.dark-mode {
    border: 3px solid var(--dark-mode-border-color);
    border-radius: 8px;
    background-color: var(--dark-mode-bg-color);
    transition-duration: 1s;
    color: white;
  }
}

@media (min-width: 521px) {
  .activator.light-mode {
    border: 3px solid var(--light-mode-border-color);
    border-radius: 8px;
    background-color: var(--light-mode-bg-color);
    transition-duration: 1s;
    color: black;
  }

  .activator.light-mode:hover {
    cursor: pointer;
    background-color: var(--light-mode-border-color);
    color: white;
  }

  .activator.dark-mode {
    border: 3px solid var(--dark-mode-border-color);
    border-radius: 8px;
    background-color: var(--dark-mode-bg-color);
    transition-duration: 1s;
    color: black;
  }

  .activator.dark-mode:hover {
    cursor: pointer;
    background-color: var(--dark-mode-border-color);
    color: white;
  }
}

.legend-type {
  font-family: "Roboto Mono", monospace;
}

@media (max-width: 547px) {
  .types {
    display: flex;
    flex-direction: column;
  }

  .type {
    padding: 5px;
    margin: 5px;
  }
}

@media (min-width: 548px) {
  .types {
    display: flex;
    flex-direction: row;
  }
}

.type {
  flex: 0.5 1;
}

.link {
  color: white;
  text-decoration: none;
}

.link:hover {
  cursor: pointer;
  text-decoration: underline;
}

.mode-selector.light-mode {
  background-color: rgb(226, 34, 34);
}

.mode-selector.april-fools-light-mode {
  background-color: rgb(255, 255, 75);
}

.mode-selector.dark-mode {
  background-color: rgb(174, 24, 24);
}

.banner-container.dark-mode {
  background-color: black;
}

.banner-container.light-mode {
  background-color: white;
}

.wrap {
  flex-wrap: wrap;
}

.easy {
  color: green;
}

.medium {
  color: yellow;
}

.hard {
  color: #800000;
}

.required {
  color: red;
}

hr.vertical {
  margin: 0;
  padding: 0;
  border: none;
  border-left: 1px solid black;
  height: 100%;
  transform: rotate(90deg);
}

.right {
  text-align: right;
  float: right;
}

.chosen.light-mode {
  border: 3px solid white;
  border-radius: 8px;
}

.chosen.dark-mode {
  border: 3px solid black;
  border-radius: 8px;
}

.footer-container.light-mode {
  background-color: red;
}

.footer-container.dark-mode {
  background-color: rgb(161, 0, 0);
}

.footer-text {
  font-family: "Work Sans", sans-serif;
  font-weight: 300;
}

h4.footer-text {
  font-size: large !important;
}

h6.footer-text {
  font-size: medium !important;
}

b {
  font-weight: bold;
}

.disabled {
  background-color: red;
  border-radius: 8px;
  color: white;
  transition-duration: 1s;
  opacity: 0.4;
}

.disabled.light-mode {
  border: 3px solid red;
}

.disabled.dark-mode {
  border: 3px solid var(--dark-mode-border-color);
}

.disabled:hover {
  cursor: not-allowed;
}
